import core from '@/core';
import { Self } from '@/vue-core';
import { alas, status } from '@/alas';
import { defineComponent, reactive } from '@vue/composition-api';
export default defineComponent({
    props: {},
    setup(props, context) {
        let self = new Self(context);
        // =================
        //
        // state
        //
        let $ = reactive({
            core,
            alas,
            copy: status.fetch('copy'),
            info: '',
            step: null,
            index: null,
            dialog: false
        });
        // =================
        //
        // methods
        //
        let open = (step, index) => {
            $.step = step;
            $.index = index;
            $.dialog = true;
        };
        let getTypeName = (type) => {
            if (type === 'action') {
                return '動作';
            }
            if (type === 'system') {
                return '系統';
            }
            if (type === 'verify') {
                return '驗證';
            }
            if (type === 'engineer') {
                return '開發人員專用';
            }
            if (type === 'custom') {
                return '自訂';
            }
            return type;
        };
        let addTemplate = (name, template) => {
            let props = {};
            for (let key in template.props) {
                props[key] = template.props[key].default;
            }
            $.dialog = false;
            $.step.templates.once('$writeSuccess', () => self.emit('add', $.index));
            $.step.templates.write({
                name,
                props
            }, {
                insert: $.index + 1
            });
        };
        let addPasteTemplate = (index) => {
            $.copy.$m.pasteTemplate({
                step: $.step,
                insert: $.index,
                index
            });
            $.dialog = false;
        };
        let removeTemplate = (index) => {
            $.copy.$m.removeTemplate(index);
        };
        // =================
        //
        // done
        //
        return {
            $,
            open,
            getTypeName,
            addTemplate,
            addPasteTemplate,
            removeTemplate
        };
    }
});
