import { __awaiter } from "tslib";
import core from '@/core';
import Help from './components/help.vue';
import TempBtn from './components/temp-btn.vue';
import Template from './components/template.vue';
import Variable from './components/variable.vue';
import { copy } from '@/utils';
import { Self } from '@/vue-core';
import { beautify } from '@/requests';
import { alas, status, action } from '@/alas';
import { defineComponent, reactive, onMounted, computed, ref } from '@vue/composition-api';
export default defineComponent({
    props: {},
    components: {
        'self-help': Help,
        'self-temp-btn': TempBtn,
        'self-variable': Variable,
        'self-template': Template
    },
    setup(props, context) {
        let self = new Self(context);
        // =================
        //
        // refs
        //
        let help = ref(null);
        let create = ref(null);
        let update = ref(null);
        let invoke = ref(null);
        let createTemplate = ref(null);
        let templates = ref(null);
        // =================
        //
        // state
        //
        let $ = reactive({
            alas,
            project: status.fetch('project'),
            copy: status.fetch('copy'),
            spec: null,
            write: false,
            writeContent: '',
            variable: false,
            renameName: '',
            createName: ''
        });
        // =================
        //
        // computed
        //
        let canPasteStep = computed(() => {
            return $.copy.step != null;
        });
        let specs = computed(() => {
            return $.project.specs.items.filter(s => s.id !== $.spec.id).map(s => {
                return {
                    id: s.id,
                    name: s.$v.typeAndName
                };
            });
        });
        // =================
        //
        // mounted
        //
        onMounted(() => {
            $.spec = $.project.specs.items.find(s => s.id === self.route.params.id);
        });
        // =================
        //
        // methods
        //
        let openCreateTemplate = (step, index) => {
            createTemplate.value.open(step, index);
        };
        let invokeTest = () => {
            $.project.$o.save.start();
            invoke.value.play([$.spec.id]);
        };
        let createStep = () => {
            create.value.open(() => {
                $.spec.steps.write({
                    name: $.createName
                });
            });
        };
        let updateStep = (step) => {
            $.renameName = step.name;
            update.value.open(() => {
                step.name = $.renameName;
            });
        };
        let removeStep = (id) => {
            action.confirm('確定刪除步驟？', done => {
                $.spec.steps.remove(id);
                done();
            });
        };
        let openTemplateEdit = (index) => {
            setTimeout(() => {
                templates.value[index + 1].$.edit = true;
            }, 100);
        };
        let openHelp = (template) => {
            help.value.open(template);
        };
        let openWrite = (template) => __awaiter(this, void 0, void 0, function* () {
            $.write = true;
            $.writeContent = yield beautify(core.templates[template.name].write(template.props), 4);
        });
        let copyCode = () => {
            copy($.writeContent);
            action.message('success', '已複製到剪貼簿');
        };
        let copyStep = (step) => {
            $.copy.$m.copyStep(step);
        };
        let pasteStep = ({ spec, index }) => {
            $.copy.$m.pasteStep({ spec, index });
        };
        // =================
        //
        // done
        //
        return {
            $,
            specs,
            openCreateTemplate,
            invokeTest,
            createStep,
            updateStep,
            removeStep,
            openTemplateEdit,
            openHelp,
            openWrite,
            copyCode,
            help,
            create,
            update,
            invoke,
            createTemplate,
            templates,
            copyStep,
            canPasteStep,
            pasteStep
        };
    }
});
